<template>
<div>
    <van-picker
        show-toolbar
        :columns="columns"
        :default-index="defaultIndex"
        @cancel="$emit('close')"
        @confirm="onConfirm"
        @change="onGenderChange"
    />
</div>
</template>
<script>
import {updataUserProfile} from '../api/user'
export default {

props:{
    value:{
        type:Number,
        required:true
    }
},
data(){
return {
    columns: ['',''],
    defaultIndex:this.value
}
},
methods:{
    //   Picker 
    onGenderChange(picker,value,index) {
        this.defaultIndex = index
    },

    // 
    async onConfirm() {
    this.$toast.loading({
            message:'...',
            forbidClick:true // 
        })
          // 
          await updataUserProfile({
            gender:this.defaultIndex
        })
            //  -->  --> 
            this.$toast.success('')
            //  v-model  input
            this.$emit('input',this.defaultIndex)
            this.$emit('close')
    }
    
},
components:{

},
};
</script>

