<template>
<div>
    <van-datetime-picker
    v-model="defaultBrithday"
    type="date"
    title=""
    :min-date="minDate"
    :max-date="maxDate"
    @cancel="$emit('close')"
    @confirm="onConfirm"
    @change="onBrithdayChange"
    />
</div>
</template>
<script>
import {updataUserProfile} from '../api/user'
export default {

props:{
    value:{
        type:String,
        required:true
    }
},
data(){
return {
    defaultBrithday:new Date(this.value),
    minDate: new Date(1990, 0, 1),
    maxDate: new Date(),
    // currentDate: new Date(2021, 0, 17)
}
},
methods:{
    //   picker: Picker 
    onBrithdayChange(picker) {
        this.defaultBrithday = picker
    },
     // 
     async onConfirm() {
    this.$toast.loading({
            message:'...',
            forbidClick:true // 
        })
        const date = `${this.defaultBrithday.getFullYear()}-${this.defaultBrithday.getMonth()+1}-${this.defaultBrithday.getDate()}`
          // 
          await updataUserProfile({
            birthday:date
        })
            //  -->  --> 
            this.$toast.success('')
            //  v-model  input
            this.$emit('input',date)
            this.$emit('close')
    }
    
},
components:{

},
};
</script>

