<template>
<div class="user-profile">
    
   <!--  -->
   <van-nav-bar
      class="app-nav-bar"
      title=""
      left-arrow
      @click-left="$router.back()" />

        <!--  -->
        <input type="file" 
        hidden ref="file" 
        accept="image/*"
        @change="onFileChange"
        >
      <van-cell title="" 
      is-link center
      @click="$refs.file.click()"
      >
        <van-image
            width="30"
            height="30"
            round
            :src="user.photo"
        />
      </van-cell>

      <van-cell 
      title="" 
      is-link 
      :value="user.name"
      @click="isEditNameShow=true"
      />
      
      <van-cell 
      title="" 
      is-link 
      :value="user.gender===0 ? '' : '' "
      @click="isEditGenderShow = true"
      />

      <van-cell 
      title="" 
      is-link 
      :value="user.birthday"
      @click="isEditBrithdayShow=true"
      />
      <!--  -->
        <!--  -->
        <van-popup 
        v-model="isEditNameShow" 
        position="bottom" 
        :style="{ height: '100%' }"
        >
            <updata-name 
            v-if="isEditNameShow"
            @close="isEditNameShow=false"
            :name="user.name"
            @updata-name="user.name = $event"
            />
        </van-popup>
        <!--  -->
        <van-popup 
        v-model="isEditGenderShow" 
        position="bottom" 
        >
            <updata-gender
            v-model="user.gender"
            @close="isEditGenderShow = false" 
            />

        </van-popup>

        <!--  -->
        <van-popup 
        v-model="isEditBrithdayShow" 
        position="bottom" 
        >
            <updata-brithday
            v-if="isEditBrithdayShow"
            v-model="user.birthday"
            @close="isEditBrithdayShow = false" 
            />
        </van-popup>

         <!--  -->
         <van-popup 
         class="edit-photo"
        v-model="isEditPhotoShow" 
        position="bottom" 
        style="height:100%"
        >
          <updata-photo
          v-if="isEditPhotoShow"
          @updata-photo="user.photo = $event"
          :file="previewImage"
          @close="isEditPhotoShow = false"
           />
        </van-popup>
</div>
</template>
<script>
import {getUserProfile} from '../api/user'
import UpdataName from '../components/updata-name.vue'
import UpdataGender from '../components/updata-gender.vue'
import UpdataBrithday from '../components/updata-brithday.vue'
import UpdataPhoto from '../components/updata-photo.vue'
export default {

props:{

},
data(){
return {
    // 
    user:{},
    // 
    isEditNameShow:false,
    // 
    isEditGenderShow:false,
    // 
    isEditBrithdayShow:false,
    // 
    isEditPhotoShow:false,
    // 
    previewImage:null
}
},
created () {
  this.loadUserProfile()  
},
methods:{
   async loadUserProfile() {
    const {data} = await getUserProfile()
    this.user = data.data 
    },
    // 
    onFileChange() {
        // 
        // const blob = window.URL.createObjectURL(this.$refs.file.files[0])
        const file = this.$refs.file.files[0]
        // 
        this.previewImage = file
        // 

        // 
        this.isEditPhotoShow = true
        //  flie  value
        this.$refs.file.value = ''
    }
},
components:{
    UpdataName,
    UpdataGender,
    UpdataBrithday,
    UpdataPhoto
},
};
</script>

<style scoped lang='less'>
    .van-popup {
        background-color: #f5f7f9;
    }
    .edit-photo {
        background-color: #000;
    }
</style>