<template>
<div class="updata-nama">
    <!--  -->
    <van-nav-bar
      title=""
      left-text=""
      right-text="" 
      @click-left="$emit('close')"
      @click-right="onConfirm"
    />
    <!--  -->
    <van-field
    v-model="message"
    rows="2"
    autosize
    type="textarea"
    maxlength="16"
    placeholder=""
    show-word-limit
    />
</div>
</template>
<script>
import {updataUserProfile} from '../api/user'
export default {

props:{
    name:{
        type:String,
        required:true
    }
},
data(){
return {
    message:this.name
}
},
methods:{
   async onConfirm() {
    this.$toast.loading({
            message:'...',
            forbidClick:true // 
        })
      try{
            // 
        await updataUserProfile({
            name:this.message
        })
            //  -->  --> 
            this.$toast.success('')
            // 
            this.$emit('updata-name',this.message)
            this.$emit('close')
      }catch(err) {
        if(err && err.response && err.response.status === 409) {
            this.$toast.fail('')
        }
      }
    }
    
},
components:{

},
};
</script>

<style scoped lang='less'>
    .van-field{
        padding: 10px;
    }
</style>